:root {
  --primary: rgb(1,115,116);
  --darkPrimary:rgb(0,84,87);
  --secondary: rgb(1,162,153);
  --darkSecondary: rgb(1,149,146);
}

body {
  margin: 0;
  padding: 0;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  background-color: white;
  box-shadow: 0px 0.233px 8px rgba(216, 215, 215, 0.985);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 2rem;
}

.logo__wrapper img {
  width: 50px;
}

.logo__wrapper {
  display: flex;
  align-items: center;
}

.logo__wrapper h1 {
  font-weight: bold;
  margin: 0px;
  margin-left: 10px;
}
.navigation-menu {
  position: relative;
  display: flex;
  justify-content: space-between ;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
  padding-right: 3rem;
}

.navigation-menu li {
  position: relative;
}
.navigation-menu li a {
  font-weight: bold;
  position: relative;
  display: block;
  text-decoration: none;
  padding: 1rem 2rem;
  font-size: 20px;
  color: rgb(82, 81, 81);
}

.navigation-menu li a:hover:not(.activeNav) {
  background-color: rgba(209, 239, 238, 0.942);
  color: var(--primary);
}

.userInfoNav {
  border: 1px solid lightgray;
  padding: 5px 20px;
  border-radius: 5px;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  font-size: 14px;
}

.userInfoNav img {
  width: 30px;
  margin-right: 5px;
  border-radius: 50%;
}

.activeNav {
  background-color: whitesmoke;
}

.primaryText {
  color: var(--primary);
}

.primaryBtn {
  background-color: var(--primary);
  color: white;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  padding: 10px 30px;
  margin: 0.5rem;
}

.primaryBtn:hover {
  background-color: var(--darkPrimary);
}

.secondaryBtn {
  background-color: var(--secondary);
  color: white;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  padding: 10px 30px;
  margin: 0.5rem;
}

.secondaryBtn:hover {
  background-color: var(--darkSecondary);
}
